* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a {
  color: #333;
  text-decoration: none;
}

.eachTodo {
  background-color: #c9e3ff;
  border-bottom: 1px black solid;
  padding: 10px;
}
.container {
  padding: 0;
}

.btn {
  display: inline-block;
  border: none;
  background: #1ef7e9;
  color: #000;
  padding: 7px 20px;
  cursor: pointer;
}

.btn:hover {
  background: greenyellow;
  color: #000;
}

.center {
  margin: auto;
  width: 50%;
  max-width: 400px;
  min-width: 150px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 20px;
}
div.loader {
  font-size: 100px;
}

.loaded {
  font-size: 1.2em;
  font-weight: bold;
  margin-bottom: 10px;
}
